import React, { useState, useEffect } from "react";
import articleContent from "./article-content";
import ArticleList from "../components/articleList";
import NotFoundPage from "../pages/NotFoundPage";
import { fetch } from "whatwg-fetch";

import CommentList from '../components/commentsList';
import CommentsForm from '../components/commentsForm';
import UpvotesSection from '../components/upvotesSection';


const ArticlePage = ({ match }) => {
  const name = match.params.name;
  const article = articleContent.find(article => article.name === name);

  const [articleInfo, setArticleInfo] = useState({ upvotes: 0, comments: [] });

  const postData = () => {
   
     fetch(`/api/article/${name}/upvote/`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json"
        }
      });
  };


const pullData = (event) => (
  console.log(this.value())
)


  //   const otherArticles = articleContent.filter( article => article.name !== name);
  useEffect(() => {
    const fetchData = async () => {
      const result = await fetch(`/api/article/${name}/`);
      const body = await result.json();
      
      setArticleInfo(body);
    };
    fetchData();
  }, [articleInfo.comments, name]);
  if (!article) return <NotFoundPage />;
  return (
    <React.Fragment>
      <h1> {article.title} </h1>

      <UpvotesSection name = {name} upvotes = {articleInfo.upvotes} setArticleInfo = {setArticleInfo}/>
      

      {article.content.map((paragraph, key) => (
        <p key={key}> {paragraph} </p>
      ))}

      <hr />
     
      <CommentList comments={articleInfo.comments} />
      <CommentsForm name={name} setArticleInfo={setArticleInfo} />
     

     
      <h2>Other Articles</h2>
      <ArticleList
        articles={articleContent.filter(article => article.name !== name)}
      />
    </React.Fragment>
  );
};

export default ArticlePage;
