import React from 'react';

const CommentList = ({comments}) => (
    <React.Fragment>
    <h3>Comments</h3>
    {comments.map((comments, key) => (
        
        <div key={key} className="comment">
        <h4>{comments.username} <span>said: </span></h4> 
        <p>{comments.text}</p> 
       
        </div>
      ))}
      </React.Fragment>
    )

export default CommentList;